.bubbleButton {
  display: flex;
  background-color: var(--offwhite);
  color: var(--backgroundgray);
  font-family: 'Helvetica Neue', sans-serif;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  text-decoration: none;
  border-radius: 100vw; // Massive to make sure it's properly rounded
  text-shadow: 0px 0px 0px var(--backgroundgray);

  padding: 20px 4vw;
  font-size: 1.3vw;
  margin-right: 2vw;
  transition: color 0.5s cubic-bezier(0.25, 0.1, 0.25, 1), background-color 0.5s cubic-bezier(0.25, 0.1, 0.25, 1), text-shadow 0.5s cubic-bezier(0.25, 0.1, 0.25, 1);
}

.bubbleButton:hover {
  background-color: #820000;
  color: var(--offwhite);
  text-shadow: 0px 0px 10px var(--offwhite);
}

.profile-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin: 3svw;
  z-index: 10;

  &__photo {
    width: 20svw;
    height: 20svw;
    border-radius: 50%;
    object-fit: cover;
  }

  &__description {
    text-align: center;
    h2 {
      font-size: 2em;
      font-weight: bold;
      color: var(--offwhite);
      font-family: 'Helvetica Neue', sans-serif;
    }
    p {
      @media (max-width: 800px) {
        font-size: 1em;
      }
      @media (min-width: 800px) {
        font-size: 1.5em;
      }
      color: var(--getinvolvedgray);
      font-family: 'Libre Caslon Text', serif;
    }
  }
}